import { ChangeDetectionStrategy, Component, OnChanges } from '@angular/core';
import { getLoanAlias, Loan, LoanInfoComponent, LoanItemCustomBase } from '@backbase/loans-collection-ui-ang';

@Component({
  selector: 'bb-loan-info-retail',
  templateUrl: 'loan-info-retail.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LoanInfoRetailComponent extends LoanInfoComponent<Loan, LoanItemCustomBase> implements OnChanges {
  loanAlias!: string | undefined;

  ngOnChanges(): void {
    super.ngOnChanges();
    this.loanAlias = getLoanAlias(this.loan);
  }
}
